import React, { useState, useEffect } from "react";
import { Container, Form, Button, Row, Col } from "react-bootstrap";
import DropzoneComponent from "./FileUpload";
import YAML from "js-yaml";
import { Editor } from "@monaco-editor/react";

const App = () => {
  const [formData, setFormData] = useState({
    version: "1.0.0",
    configuration: {
      source: "SDEV",
      baseDirectory: "./Sources",
      logLevel: "DEBUG",
    },
    transferts: [
      {
        name: "COPYBOOK",
        description: "Copybook Cobol",
        source: "DTOPT.TOP.COPYBOOK",
        destination: "/CPY",
        extensionFile: ".cpy",
      },
    ],
    hosts: [
      {
        name: "SDEV",
        description: "LPAR de développement",
        type: "zos",
        hostname: "sdev.toto",
        port: 7554,
      },
    ],
  });

  const [yamlData, setYamlData] = useState("");

  const [error, setError] = useState("");

  useEffect(() => {
    const yamlString = YAML.dump(formData);
    setYamlData(yamlString);
  }, [formData]);
  const handleChange = (e) => {
    const { name, value } = e.target;
    if (name === "source" || name === "baseDirectory" || name === "logLevel") {
      setFormData({
        ...formData,
        configuration: {
          ...formData.configuration,
          [name]: value,
        },
      });
    } else {
      setFormData({
        ...formData,
        [name]: value,
      });
    }
  };

  const handleYamlChange = (value) => {
    setYamlData(value);

    try {
      const jsonObject = YAML.load(value);
      setFormData(jsonObject);
      setError("");
    } catch (err) {
      setError("Invalid YAML syntax");
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    try {
      const yamlString = YAML.dump(formData);
      setYamlData(yamlString);
      setError("");
    } catch (err) {
      setError("Error converting to YAML");
    }
  };

  //ajout d'un bloc de formulaire pour les transferts
  const handleAddTransfert = () => {
    const newTransfert = {
      name: "TRANSFERT",
      description: "",
      source: "",
      destination: "",
      extensionFile: "",
    };
    setFormData({
      ...formData,
      transferts: [...formData.transferts, newTransfert],
    });
  };

  const handleInputChange = (index, event) => {
    const values = [...formData.transferts];
    values[index][event.target.name] = event.target.value;
    setFormData({ ...formData, transferts: values });
  };

  return (
    <div style={{ backgroundColor: "#f8f9fa" }}>
      <Container style={{ backgroundColor: "#ffffff" }}>
        <Row className="justify-content-center text-center">
          <Col>
            <h1 className="my-3">zInitFile Generator</h1>
            <br />
          </Col>
        </Row>
        <Row className="justify-content-center text-center">
          <Col>
            <DropzoneComponent />
            <br />
          </Col>
        </Row>
        <Row>
          <Col md={6} className="border" style={{ margin: "5px" }}>
            <Form>
              <Row style={{ backgroundColor: "#ffffdd", margin: "5px" }}>
                <Col md={12}>
                  <h5>Version</h5>
                </Col>
                <Col md={12}>
                  <Form.Group controlId="version">
                    <Form.Label>version</Form.Label>
                    <Form.Select
                      value={formData.version}
                      onChange={handleChange}
                      name="version"
                    >
                      <option value="1.0.0">1.0.0</option>
                      <option value="2.0.0">2.0.0</option>
                    </Form.Select>
                  </Form.Group>
                  <br />
                </Col>
              </Row>
              <Row style={{ backgroundColor: "#ffffdd", margin: "5px" }}>
                <Col md={12}>
                  <h5>Configuration</h5>
                </Col>
                <Col md={12}>
                  <Form.Group controlId="source">
                    <Form.Label>Source</Form.Label>
                    <Form.Control
                      type="text"
                      name="source"
                      value={formData.configuration.source}
                      placeholder="Enter source"
                      onChange={handleChange}
                    />
                  </Form.Group>

                  <Form.Group controlId="baseDirectory">
                    <Form.Label>baseDirectory</Form.Label>
                    <Form.Control
                      type="text"
                      name="baseDirectory"
                      value={formData.configuration.baseDirectory}
                      placeholder="Enter baseDirectory"
                      onChange={handleChange}
                    />
                  </Form.Group>

                  <Form.Group controlId="logLevel">
                    <Form.Label>logLevel</Form.Label>
                    <Form.Select
                      aria-label="Default select example"
                      value={formData.configuration.logLevel}
                      onChange={handleChange}
                      name="logLevel"
                    >
                      <option value="ALL">ALL</option>
                      <option value="DEBUG">DEBUG</option>
                      <option value="ERROR">ERROR</option>
                    </Form.Select>
                  </Form.Group>
                  <br />
                </Col>
              </Row>

              <Row style={{ backgroundColor: "#ffffdd", margin: "5px" }}>
                <Col md={12}>
                  <h5>Transferts</h5>
                </Col>
                <Col md={12}>
                  <Button variant="secondary" onClick={handleAddTransfert}>
                    Add Transfer
                  </Button>
                  <br />
                </Col>
                {formData.transferts.map((transfert, index) => (
                  <Form key={index}>
                    <Form.Group controlId={`name-${index}`}>
                      <Form.Label>Name</Form.Label>
                      <Form.Control
                        type="text"
                        placeholder="Enter name"
                        value={transfert.name}
                        name="name"
                        onChange={(event) => handleInputChange(index, event)}
                      />
                    </Form.Group>
                    <Form.Group controlId={`description-${index}`}>
                      <Form.Label>Description</Form.Label>
                      <Form.Control
                        type="text"
                        placeholder="Enter description"
                        value={transfert.description}
                        name="description"
                        onChange={(event) => handleInputChange(index, event)}
                      />
                    </Form.Group>

                    <Form.Group controlId={`source-${index}`}>
                      <Form.Label>Source</Form.Label>
                      <Form.Control
                        type="text"
                        placeholder="Enter source"
                        value={transfert.source}
                        name="source"
                        onChange={(event) => handleInputChange(index, event)}
                      />
                    </Form.Group>

                    <Form.Group controlId={`destination-${index}`}>
                      <Form.Label>Destination</Form.Label>
                      <Form.Control
                        type="text"
                        placeholder="Enter destination"
                        value={transfert.destination}
                        name="destination"
                        onChange={(event) => handleInputChange(index, event)}
                      />
                    </Form.Group>

                    <Form.Group controlId={`extensionFile-${index}`}>
                      <Form.Label>Extension File</Form.Label>
                      <Form.Control
                        type="text"
                        placeholder="Enter extension file"
                        value={transfert.extensionFile}
                        name="extensionFile"
                        onChange={(event) => handleInputChange(index, event)}
                      />
                    </Form.Group>
                    {/* Repeat for other fields... */}
                  </Form>
                ))}
              </Row>
              <Row style={{ backgroundColor: "#ffffdd", margin: "5px" }}>
                <Col md={12}>
                  <h5>Hosts</h5>
                </Col>
                <Col md={12}>
                  <Form.Group controlId="name">
                    <Form.Label>name</Form.Label>
                    <Form.Control
                      type="text"
                      name="name"
                      value="SDEV"
                      readOnly
                    />
                  </Form.Group>

                  <Form.Group controlId="description">
                    <Form.Label>description</Form.Label>
                    <Form.Control
                      type="text"
                      name="description"
                      value="LPAR de développement SGRF"
                      readOnly
                    />
                  </Form.Group>

                  <Form.Group controlId="type">
                    <Form.Label>type</Form.Label>
                    <Form.Control
                      type="text"
                      name="type"
                      value="zos"
                      readOnly
                    />
                  </Form.Group>

                  <Form.Group controlId="hostname">
                    <Form.Label>hostname</Form.Label>
                    <Form.Control
                      type="text"
                      name="hostname"
                      value="sdev.socgen.dns21"
                      readOnly
                    />
                  </Form.Group>

                  <Form.Group controlId="port">
                    <Form.Label>port</Form.Label>
                    <Form.Control
                      type="text"
                      name="port"
                      value="7554"
                      readOnly
                    />
                  </Form.Group>
                  <br />
                </Col>
              </Row>
              {/* Repeat similar structures for other main fields */}
              <br />
              <Button variant="primary">Generate YAML</Button>
            </Form>
          </Col>
          <Col className="border" style={{ margin: "5px" }}>
            <Form.Group
              controlId="formYamlOutput"
              className="mt-3"
              style={{ height: "100%" }}
            >
              <Form.Label>YAML Output</Form.Label>
              <Editor
                height="1000px"
                language="yaml"
                theme="vs-dark"
                value={yamlData}
                onChange={(value) => handleYamlChange(value)}
                options={{
                  automaticLayout: true,
                  minimap: { enabled: false },
                  line: 1,
                }}
              />
            </Form.Group>
          </Col>
        </Row>
        <br />
        <br />
        <br />
        <br />
      </Container>
    </div>
  );
};

export default App;
